.habitDetail-container{
    /* height: 100vh; */
    width: 100%;
    background-color: rgb(255, 255, 255);
    padding: 10px;
}
.back-btn-icon{
    cursor: pointer;
    position: fixed;
    top: 0px;
    left: 0px;
    /* color: white; */
    transition: all 0.2s;
    background-color: rgba(207, 207, 207, 0.3);
    width: 100%;
    z-index: 2;
    backdrop-filter: blur(5px);
    padding: 10px;
}
.back-btn-icon:hover{
    color: var(--primary-color);
}
.back-btn-icon span{
    margin-left: 8px;
}


.habit-details{
    margin-top: 30px;
}
.habit-detail-cat-time{
    display: flex;
    flex-direction: row;
    gap: 5px;
    margin-bottom:7px;
    
}

.habit-detail-current-date{
    margin: 4px 0;
}
.habit-detail-cat-time div{
    padding: 4px 10px;
    background-color: rgba(128, 128, 128, 0.25);
    border-radius: 20px;
}

/* .habit-time-everyday{
    margin-left:10px;
} */
.habit-details-records{
    display: flex;
    width: 100%;
    gap: 10px;
    overflow: auto;
}

.habit-records-data{
    /* width: 250px; */
    min-width: 200px;
    /* border: 1px solid; */
    width: auto;
    padding: 10px;
    border-radius: 10px;
    color: rgba(255, 255, 255, 0.798);
    transition: all 0.4s;
}
.habit-records-data:hover{
    color: rgb(255, 255, 255);
}
.current-streak{
    background: linear-gradient(rgb(168, 88, 233), rgb(68, 121, 255));
    box-shadow: 0px 20px 63px -31px rgb(97, 68, 255);
}
.habit-finished{
    background: linear-gradient(rgb(61, 231, 96), rgb(29, 212, 149));
    box-shadow: 0px 20px 63px -31px rgb(29, 212, 149);
}
.completion-rate{
    background: linear-gradient(rgb(237, 49, 102), rgb(231, 68, 193));
    box-shadow: 0px 20px 63px -31px rgb(188, 16, 148);
}


.todays-completition-stats{
    margin-top: 10px;
}

.habit-completion-graph{
    width: 100%;
    max-width: 500px;
    background-color: rgba(165, 165, 165, 0.134);
    padding: 20px;
    border-radius: 10px;
    margin-top: 7px;
}


/* .ant-progress-inner{
    width: auto !important;
    height: auto !important;
} */
.ant-progress-inner{
    width:90% !important;
    height: 100% !important;
    /* max-width: 300px; */
}
.ant-progress-circle{
    width: 100%;
    max-height: 400px;
}
/* 
.ant-progress-outer .ant-progress-inner{
    width: 100% !important;
    height: 10px !important;
} */

.ant-progress-outer .ant-progress-bg{
    height: 100% !important;
}
.ant-progress-text{
    font-size: 35px !important;
}


/* Completed Stats */
.complete-task-count{
    display: flex;
    align-items: center;
    gap: 10px;
}

.complete-task-count div{
    background-color: rgb(244, 244, 244);
    padding: 5px;
    border-radius: 50px;
    display: flex;
    align-items: center;
    gap: 20px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}


.dec-count-btn{
    border-radius: 50%;
    background-color: rgb(255, 181, 181);

}
.inc-count-btn{
    border-radius: 50%;
    background-color: rgb(129, 225, 129);
}

.dec-count-btn:hover{
    background-color: red;
    color: white !important;
}

.inc-count-btn:hover{
    background-color: green;
    color: white !important;
}

