/* Current task Status */
.current-task-status{
    width: 170px;
    display: flex;
    align-items: center;
    margin-top: 10px;
}

.current-task-status .todays-habit-status{
    width: 100%;
}


.previous-data{
    margin-top: 10px;
}

.previous-record{
    border-radius: 10px;
    background-color: rgba(165, 165, 165, 0.134);
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-height: 100%;
    overflow: auto;
}

.previous-day{
    /* background: rgb(156, 156, 156); */
    /* box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px; */
    /* box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px; */
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border-radius: 10px;
    padding: 10px;
    color: rgb(40, 40, 40);
    transition: all 0.4s;
    display: flex;
    justify-content: space-between;
}
.previous-day:hover{
    background: rgb(255, 72, 103);
    color: rgb(255, 255, 255);
}
.previous-date{
    margin-bottom: 10px;
}

/* .previous-habit-status-cont{

} */

.previous-habit-status{
    width: 120px;
}
.prevrecord-antDProgress{

}
.antD-progress .ant-progress-circle{
    height: 50px;
}
.antD-progress .ant-progress-text{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px !important;
}

.antD-progress .ant-progress-text svg{
    height: 20px;
}

.ant-select-selector{
    background-color: rgba(255, 255, 255, 0.87) !important;
    margin-left:10px !important;
}
.delete-habit{
    /* width: 100px; */
    margin: auto;
    /* background-color: red; */
    /* color: white; */
    color: red;
    border: 1px solid red;
    border-radius: 10px;
    padding: 10px 15px;
    margin:auto;
    margin-top: 10px;
    width: 150px;
    text-align: center;
    cursor: pointer;
    transition: all 0.3s;
}

.delete-habit:hover{
    background-color: red;
    color: white;
}